import Link from 'next/link';

const FetchRepos  = props => (
    <div style={Styles.div}>
        { props.collection.map(item => (
          <div key={item.id} style={Styles.repoCard}>
            <Link href={item.html_url}>
              <a target='_blank' style={Styles.link}><h4>{item.name}</h4></a>
            </Link>
            <p>{item.description}</p>
          </div>
        )) }
    </div>
)

export default FetchRepos;

const Styles = {
  div: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-evenly',
    padding: '0 100px'
  },
  repoCard: {
    backgroundColor: 'gray',
    borderRadius: '25px',
    color: 'rgb(243, 243, 243)',
    display: 'flex',
    flexDirection: 'column',
    margin: '25px',
    padding: '25px',
    minHeight: '300px',
    minWidth: '300px',
    maxHeight: '300px',
    maxWidth: '300px',
    textAlign: 'center'
  },
  link: {
    color: 'rgb(243, 243, 243)',
    textTransform: 'uppercase',
    textAlign: 'center'
  }
}